<template>
  <div class="promotions" style="padding: 30px 0;">
    <ul class="pr-content">
      <li class="in pr-1" v-for="(item,index) in yhhdList" :key="index">
        <div class="main">
          <div class="img">
            <img
              :src="item.imgUrl"
              alt=""
            />
          </div>
          <div class="r-side">
            <div class="r-side-top">
              <p class="title">{{item.title}}</p>
            </div>
            <div class="r-side-bottom">
              <!-- <p>活动时间:{{item.addTime}}</p> -->
            </div>
            <div class="r-side-mid">
              
            </div>
            <router-link :to="`/detail?typeName=discount&id=${item.id}&title=${item.title}`"><p class="btn">查看详情&gt;&gt;</p></router-link>
          </div>
        </div>
        <div class="time-line">
          <div class="in-b"></div>
          <div class="date-time">{{item.addTime}}</div>
        </div>
       
      </li>
      
    </ul>
  </div>
</template>

<script>
import { discountList } from "../api/public";
export default {
  data() {
    return {
      tabCur: 0,
      yhhdList: [],
      curPage: 1,
      total: 1,
      limit: 100000,
    };
  },
  mounted() {
    this.discountList();
  },
  methods: {
    discountList() {
      let _this = this;
      discountList({
        page: _this.curPage,
        limit: _this.limit,
      }).then((res) => {
        console.log(res);
        if (res.code == 1) {
          this.yhhdList = res.data;
          this.total = res.count;
        }
      });
    },
  },
};
</script>

